body {
  height: 98vh;
  padding: 0;
}
body {
  background-color: rgb(219, 220, 255);
  background-size: 35px 35px;
  background-image:
    linear-gradient(to right, rgba(0, 0, 255, 0.3) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(0, 0, 255, 0.3) 1px, transparent 1px);
}
h1 {
  margin: 0;
}
.desktop-icons {
  position: absolute;
  margin: 1rem 0 0 2rem;
}
.icon {
  text-align: center;
  margin-top: 1.5rem;
}
.icon img {
  width: 2rem;
  height: auto;
  transition: all ease-in-out 100ms;
  cursor: pointer;
}
.icon img:hover {
  transform: translateY(-5px);
}
/*Top Bar*************************************************************/
.desktop-top-bar {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: right;
  padding: 0.4rem;
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid rgb(0, 0, 0);
  border-radius: 5px;
  margin: 0.5rem;
  box-shadow: 6px 6px 2px rgba(6, 0, 55, 0.4);
}
.desktop-top-bar img {
  width: 1.2rem;
  height: auto;
}
.link-wrapper {
  display: flex;
  flex-direction: row;
  margin-right: 1rem;
}
.link-wrapper a {
  padding: 0 1rem 0 1rem;
}
.link-wrapper img {
  transition: all ease-in-out 100ms;
}
.link-wrapper img:hover {
  transform: translateY(-3px);
}
.link-wrapper .time {
  font-weight: 500;
  font-size: 1.2rem;
}